/****select menu****/
select {
  width: 100%;
  height: 50px;
  font-size: 100%;
  font-weight: bold;
  cursor: pointer;
  border-radius: 0;
  background-color: #860e86;
  border: none;
  border-bottom: 2px solid #860e86;
  color: white;
  appearance: none;
  padding: 10px;
  padding-right: 38px;
  -webkit-appearance: none;
  -moz-appearance: none;
  transition: color 0.3s ease, background-color 0.3s ease, border-bottom-color 0.3s ease; }

/* For IE <= 11 */
select::-ms-expand {
  display: none; }

.ekutumb-select-icon {
  position: absolute;
  top: 22px;
  right: 4px;
  width: 30px;
  height: 36px;
  pointer-events: none;
  border: 2px solid #ccd0d3e7;
  padding-left: 5px;
  transition: background-color 0.3s ease, border-color 0.3s ease; }

.ekutumb-select-icon svg.icon {
  transition: fill 0.3s ease;
  fill: #DCDCDC; }

select:hover,
select:focus {
  color: #fff;
  background-color: #860e86;
  border-bottom-color: #DCDCDC; }

select:hover ~ .select-icon,
select:focus ~ .select-icon {
  background-color: #860e86;
  border-color: #860e86; }

select:hover ~ .ekutumb-select-icon svg.icon,
select:focus ~ .ekutumb-select-icon svg.icon {
  fill: #ccc; }

.ekutumb-dropdown-container {
  width: auto;
  margin: 0 auto;
  position: relative; }
